<template>
    <div class="page-content">
        <div class="page-block mb30 support-block">
            <h2>Если у вас возникли вопросы или предложения по улучшению сервиса, то создайте заявку и мы оперативно
                ответим на неё</h2>
            <div class="content">
                <button class="button-gradient" @click="formOpenHandler()">Создать заявку</button>
                <div class="select-wrap-short">
                    <label>Ваши заявки</label>
                    <v-select class="select" v-model="apply" placeholder="Ваши заявки" :from="applies"></v-select>
                </div>
            </div>
        </div>

        <div class="news-block-wrapper page-block page-padding-y">
            <h1>Свежие новости</h1>
            <div class="news-block">
                <div v-for="article in news" :key="article.id" class="news-item flex-between align-top">
                    <div class="text">
                        <h3>{{article.title}}</h3>
                        <p>{{article.text}}</p>
                    </div>
                    <div class="image">
                        <img :src="genImageUrl(article)" alt="">
                    </div>
                </div>
            </div>
            <div class="load-more">Ещё новости</div>
        </div>

        <div class="support__layer" v-if="form_open" @click.self="form_open = false">
            <div class="support__form">
                <div class="title">Новая заявка</div>
                <div class="input-flex input-wrap mb20">
                    <label class="calc-block__w1">Тема сообщения</label>
                    <input v-model="type" type="text">
                </div>
                <div class="input-flex input-wrap mb20">
                    <label class="calc-block__w1">Номер заказа</label>
                    <input v-model="number" type="text">
                </div>
                <div class="input-flex select-wrap mb20">
                    <label class="calc-block__w1">Тип проблемы</label>
                    <v-select class="select" placeholder="Тип проблемы" v-model="problem" :from="problems"></v-select>
                </div>
                <div class="input-flex input-wrap mb20">
                    <label class="calc-block__w1"></label>
                    <textarea v-model="text" placeholder="Введите сообщение"></textarea>
                </div>
                <div class="support__form__button flex-start">
                    <label class="calc-block__w1"></label>
                    <button class="button-gradient">Отправить сообщение</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Support",
        data: () => ({
            form_open: false,
            apply: 'Заявка N1',
            applies: [
                'Заявка N1',
                'Заявка N2'
            ],
            problems: [
              'Проблема 1',
              'Проблема 2'
            ],
            type: '',
            number: null,
            problem: null,
            text: ''
        }),
        props: ['news'],
        methods: {
            genImageUrl(article) {
                return `${process.env.VUE_APP_SERVER_URL}${article.image}`
            },
            formOpenHandler() {
                this.form_open = !this.form_open
                if (this.form_open) document.body.style.overflow = 'hidden'
                else document.body.style.overflow = 'visible'
            }
        }
    }
</script>

<style scoped>

</style>